@import "vars";

.info {
  display: flex;
  justify-content: space-between;
  &__heading {
    width: 314px;
    font-weight: 300;
    text-transform: uppercase;
    line-height: 60px;
    font-size: 48px;
    margin: 0;
  }
  &__text {
    max-width: 564px;
    > :first-child {
      margin: 0;
    }
    > :last-child {
      margin-bottom: 0;
    }
  }
}

@media screen and (max-width: $small-screen-breakpoint) {
  .info {
    flex-direction: column;
    &__heading {
      margin: 0 auto 16px;
      text-align: center;
    }
  }
}
